<script>
import MescrollVue from "mescroll.js/mescroll.vue";
import map from "@/map/map.js";
import qs from "qs";
const shieldExplainContent = ` <span>『火锅底料』社区是一个以匿名发言为主的社区,</span> <span> 但是可以想见的是,匿名带来的自由,可能会滑向我们所不可控的方向。 <br /> </span> <span>我们希望为大家提供畅所欲言的平台,</span> <span> <strong>但是平台的健康运行,需要我们共同的努力。</strong> <br /> </span> <span> 原则上,『火锅底料』会 <strong>避免删帖</strong> ， <br /> </span> <span> 对于一些『一般人价值判断』下有争议性的话题, <strong>则会折叠处理</strong> 。 <br /> </span> <span> 希望大家在发"性话题","引战话题","令人不适话题","政治相关话题","未经证实话题",的时候, <strong>自觉添加上标签。</strong> <br /> 现行管理制度下,管理员有权利将应该加标签却没加标签的帖子加上标签,并对发帖人以一天禁言的处罚。<br> </span> <span><strong>我们会默认折叠带上标签的内容，</strong></span><br> <span> 您也可以在左上角个人中心-过滤设置中<strong>自定义过滤方式。</strong> </span>`
function sleep() {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve();
    }, 4000);
  });
}
var shieldPost = (data, shieldPostId) => {
  try {
    if (localStorage.getItem('shieldPostId'))
      var shieldPostId = JSON.parse(localStorage.getItem('shieldPostId'))
  } catch (e) {
    var shieldPostId = undefined
  }
  if (!shieldPostId) return data;
  data.forEach(item => {
    if (shieldPostId.includes(item.id))
      item.filterLevel = 2
  })
  return data;
}
export default {
  name: "postsList",
  components: {
    MescrollVue, // 注册mescroll组件
  },
  data() {
    return {
      bottomDrawer: {
        showBottomDrawer: false,
        chooseIndex: 0,
        dislike_num: 0,
      },
      timestamp: new Date().getTime(), //当前时间
      map: map,
      loadingMessage: "正在加载",
      mescroll: null, // mescroll实例对象
      mescrollDown: {
        callback: this.refreshData,
        use: true,
      }, //下拉刷新的配置. (如果下拉刷新和上拉加载处理的逻辑是一样的,则mescrollDown可不用写了)
      mescrollUp: {
        // 上拉加载的配置.
        callback: this.getMorePosts, // 上拉回调,此处简写; 相当于 callback: function(page, mescroll) { }
        //以下是一些常用的配置,当然不写也可以的.
        onScroll: this.onScroll,
        noMoreSize: 3

      },
      snackbar: {
        show: false,
        message: "",
        color: "",
      },
      items: [],
      topPosts: [],
      openWhyFoldedDialog: false,
      category: {
        types: ["主干道", "校园", "社会", "情感", "娱乐", "体育", "其他"],
        selected: 0,
      },
      shieldExplainContent: sessionStorage.getItem('shieldExplainContent') || shieldExplainContent
    };
  },
  async created() {
    //获取置顶帖子
    let that = this;
    this.$request("get", "/forum/getTopPosts", {}, {})
      .then((res) => {
        that.topPosts = res.data;
        that.topPosts.map((item) => (item.top = true));
      })
      .catch((e) => {
        that.showSnackbar(e);
      });
  },
  mounted() {
  },
  methods: {
    onScroll(mescroll, y, isUp) {
      this.$store.change_scrollY(y);
      this.$store.change_isUp(isUp);
    },
    mescrollInit(mescroll) {
      this.mescroll = mescroll; // 如果this.mescroll对象没有使用到,则mescrollInit可以不用配置
    },
    showBottomDrawer(i) {
      this.bottomDrawer.showBottomDrawer = true;
      this.bottomDrawer.chooseIndex = i;
      this.bottomDrawer.dislike_num = this.items[i].dislike_num;
    },
    //计算时间差
    timeFormat(latest_comment_time) {
      //一共这么多秒前
      let t = Math.floor(
        (new Date().getTime() - new Date(latest_comment_time).getTime()) / 1000
      );
      const d = Math.floor(t / 3600 / 24);
      const h = Math.floor(t / 3600) % 24;
      const m = Math.floor(t / 60) % 60;
      const s = Math.floor(t % 60);
      let res = "";
      if (s > 0 && m == 0 && h == 0 && d == 0) {
        res = s + "秒前";
      } else if (m > 0 && h == 0 && d == 0) {
        res = m + "分钟前";
      } else if (h > 0 && d == 0) {
        res = h + "小时前";
      } else {
        let date = new Date(
          new Date(latest_comment_time).getTime() + 8 * 3600 * 1000
        );
        return date.toJSON().substr(0, 16).replace("T", " ");
      }
      return res;
    },
    //获取屏蔽配置
    getShield() {
      try {
        var shieldTag = localStorage.getItem('shieldTag')
        var shieldTagMethod = Number(localStorage.getItem('shieldTagMethod'))
        var shieldPostWordMethod = Number(localStorage.getItem('shieldPostWordMethod'))
        var shieldPostWord = localStorage.getItem('shieldPostWord')
        return { shieldTag, shieldTagMethod, shieldPostWord, shieldPostWordMethod }
      } catch (e) {
        return { shieldTag, shieldTagMethod, shieldPostWord, shieldPostWordMethod }
      }
    },
    getMorePosts(page, mescroll) {
      if (this.$route.path != '/main/index') return;
      let that = this;
      that.$request('get', '/forum/getPosts', {
        timeToken: new Date(that.items[that.items.length - 1].latest_comment_time).getTime(),
        category: that.category.selected,
      })
        .then((res) => {
          if (page.num === 1) that.items = []
          that.$nextTick(() => {
            mescroll.endSuccess(res.data.length);
          });
          shieldPost(res.data)
          that.items.push(...res.data);
        })
        .catch(e => {
          console.log(e)
          that.showSnackbar('获取失败')
          mescroll.endErr();
        })
    },
    refreshData(mescroll) {
      if (this.$route.path != '/main/index') return;
      let that = this;
      this.$request("get", "/forum/getTopPosts", {}, {})
        .then((res) => {
          that.topPosts = res.data;
          that.topPosts.map((item) => (item.top = true));
        })
        .catch((e) => {
          that.showSnackbar(e);
        });
      that.$request('get', '/forum/getPosts', {
        timeToken: new Date().valueOf(),
        category: that.category.selected,
      })
        .then((res) => {
          shieldPost(res.data)
          that.items = res.data;

          that.$nextTick(() => {
            mescroll.endSuccess(res.data.length);
          });
        })
        .catch(e => {
          console.log(e)
          that.showSnackbar(e)
          mescroll.endErr()
        })
    },
    likeOrDislike(i, value) {
      let that = this;
      that.$request('post', '/forum/forumLikeOrDislike', {}, {
        postId: Number(that.items[i].id),
        value: value,
      })
        .then((res) => {
          that.showSnackbar(res, "ok");
          if (value == 1) that.items[i].like_num++;
          else if (value == -1) {
            that.bottomDrawer.dislike_num++;
            that.items[i].dislike_num++;
          }
        })
        .catch(e => {
          console.log(e)
          that.showSnackbar(e)
        })
    },
    shieldPost(i) {
      let postId = this.items[i].id;
      try {
        if (localStorage.getItem('shieldPostId')) {
          let shieldPostId = JSON.parse(localStorage.getItem('shieldPostId'))
          if (shieldPostId.includes(postId)) {
            this.showSnackbar('已经屏蔽过了')
            return;
          }
          shieldPostId.push(postId)
          localStorage.setItem('shieldPostId', JSON.stringify(shieldPostId))
          this.showSnackbar('屏蔽成功!', 'ok');
          this.items.splice(i, 1);
        }
        else {
          let shieldPostId = []
          shieldPostId.push(postId)
          localStorage.setItem('shieldPostId', JSON.stringify(shieldPostId))
          this.showSnackbar('屏蔽成功!', 'ok');
          this.items.splice(i, 1);
        }
      } catch (e) {
        localStorage.removeItem('shieldPostId')
        this.showSnackbar('屏蔽失败!')
      }
    },
    addCollect(i) {
      let that = this;
      that.$request('post', '/forum/addCollect', {}, {
        postId: Number(that.items[i].id),
      })
        .then((res) => {
          that.showSnackbar(res, "ok");
        })
        .catch(e => {
          that.showSnackbar(e);
        })
    },
  },
  activated() {
    //进入时读取位置
    // document.body.scrollTop = this.scrollY;
    // document.documentElement.scrollTop = this.scrollY;
    console.log(this.$store.state.scrollYLeave);
    this.mescroll.scrollTo(this.$store.state.scrollYLeave, 0);
  },
  // deactivated() {
  //   //离开时记录位置
  //   this.scrollY = this.scrollY;
  // },
  watch: {
    // "bottomDrawer.showBottomDrawer"(v) {
    //   this.$store.change_drawer(v, "postsList");
    // },
    // "$store.state.drawer"(v) {
    //   if (this.$store.state.drawerName == "postsList")
    //     this.bottomDrawer.showBottomDrawer = v;
    // },
    'openWhyFoldedDialog'(v) {
      this.$store.change_dialog(v, 'openWhyFoldedDialog')
    },
    'secretDialog'(v) {
      this.$store.change_dialog(v, 'secretDialog')
    },
    '$store.state.dialog'(v) {
      if (this.$store.state.dialogName == 'openWhyFoldedDialog')
        this.openWhyFoldedDialog = v
      else if (this.$store.state.dialogName == 'secretDialog')
        this.secretDialog = v
    }
  },
};</script>

