  top: 0;
  /* -webkit-filter: blur(1.5px);
  filter: blur(1.5px); */
}
.card_background_1 {
  left: calc(10% - 20px);
  margin-top: -6px;
}
.card_background_2 {
  left: calc(11% - 2px);
  margin-top: -6px;
}
.fold_text {
  position: absolute;
  left: 57vw;
  top: 54px;
}
.refresh_message {
  position: absolute;
  width: 100%;
}
</style>


